import { Component, OnInit } from '@angular/core';
import { PostsService } from '../service/posts.service';
import { ActivatedRoute, Router } from '@angular/router';
import { CollaborateService } from '../service/collaborate.service';

@Component({
  selector: 'app-collabs',
  templateUrl: './collabs.component.html',
  styleUrls: ['./collabs.component.css']
})
export class CollabsComponent implements OnInit {
  showSpinner:boolean=true
  postId: String
  isAdmin: boolean
  comment: String
  postWriter:String
  post:any
  num:number
  show: boolean
  isSneaking:boolean
  constructor(private postService: PostsService, private route: ActivatedRoute,
    private router:Router,
    private collabsService: CollaborateService) { }
  collabRequests: any[]
  collabs: any[]
  comments: any[]
  newreqLen:number
  title:String
  ngOnInit() {
    let currentUser = JSON.parse(localStorage.getItem('user')).username
    
    let params = this.route.snapshot.params
    
    this.postId = params['postId']
    this.postService.getPost(this.postId).subscribe(res => {
      let p = res.json()
      this.post = p.result;
      this.collabRequests = this.post.requestedForCollaboration;
      this.collabs = this.post.collaborations;
      this.comments = this.post.discussions;
      this.title=this.post.title
      if (currentUser === this.post.username) { this.isAdmin = true }
      else this.isAdmin = false
      this.num = this.collabs.length
      this.newreqLen=this.collabRequests.length
      let user = localStorage.getItem('user')

      let username = JSON.parse(user).username
     if(this.collabs.indexOf(username) >=0 || this.isAdmin){
       this.isSneaking=false
       this.postWriter =this.post.username
     }
     else{
       this.isSneaking=true
       this.router.navigate(['/notfound'])
     }
    this.showSpinner=false
    },error=>{
      alert('Opps something gone wrong')
    })

  }
  toggle() {
    this.show = !this.show
  }
  add(person) {
    let post = { accept: true }
    // console.log(this.post._id,this.post.username,post)
    this.collabsService.responseToCollab(this.post._id, person, post)
      .subscribe(res => {
        let index = this.collabRequests.indexOf(person)
        this.collabs.push(person)
        this.collabRequests.splice(index, 1)
        this.num = this.collabs.length
      })
  }
  delete(person) {
    let post = { accept: false }
   
    this.collabsService.responseToCollab(this.post._id, person, post)
      .subscribe(res => {
        let index = this.collabRequests.indexOf(person)
        this.collabRequests.splice(index, 1)
      })
  }
  submit() {
    if (this.comment) {
      let user = localStorage.getItem('user')

      let username = JSON.parse(user).name
      let userComment = `${username} : ${this.comment}`
     
      this.collabsService.addDiscussion(this.postId, userComment).subscribe(res => {
        console.log(res)
        this.comments.splice(0, 0, userComment)
        this.comment = null
        this.show = !this.show

      })
    }

  }


}



