import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {RouterModule, Routes} from '@angular/router';
import {NgxPaginationModule} from 'ngx-pagination'


import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ProfileComponent } from './profile/profile.component';
import { RegisterComponent } from './register/register.component';
import {ValidateService} from './service/validate.service'
import { AuthService } from './service/auth.service';
import { PostsService } from './service/posts.service';
import { PostComponent } from './post/post.component';
import { ExploreComponent } from './explore/explore.component';
import { AuthGuard } from './auth-guard.service';
import { EditComponent } from './edit/edit.component';
import { LikeComponent } from './explore/like/like.component';
import { ViewLikesComponent } from './view-post/view-likes.component';
import { MyPipesPipe } from './pipes/my-pipes.pipe';
import { CollborateComponent } from './view-post/collborate/collborate.component';
import { CollaborateService } from './service/collaborate.service';
import { CollabsComponent } from './collabs/collabs.component';
import { ReversePipe } from './pipes/reverse.pipe';
import { ParticipationsComponent } from './participations/participations.component';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

import { IdfilterPipe } from './pipes/idfilter.pipe';
import { AboutComponent } from './about/about.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { AppErrorsComponent } from './common/app-errors/app-errors.component';
import { AppErrorHandler } from './errors/app-errors.service';
import { FooterComponent } from './footer/footer.component';
import { LoadingSpinnerComponent } from './ui/loading-spinner/loading-spinner.component';
import { SquareSpinnerComponent } from './ui/square-spinner/square-spinner.component';
import { WindowSpinnerComponent } from './ui/window-spinner/window-spinner.component';





 const appRoute:Routes=[
   {path:'',component:HomeComponent},
   {path:'login',component:LoginComponent},
   {path:'register',component:RegisterComponent},
   {path:'profile',component:ProfileComponent, canActivate:[AuthGuard]},
   {path:'dashboard',component:DashboardComponent},
   {path:'about',component:AboutComponent},
   {path:'notfound',component:NotFoundComponent},
   {path:'about',component:NotFoundComponent},
   {path:'post',component:PostComponent, canActivate:[AuthGuard]},
   {path:'explore',component:ExploreComponent, canActivate:[AuthGuard]},
   {path:'edit/:postWriter/:id',component:EditComponent, canActivate:[AuthGuard]},
   {path:'viewlikes/:id',component:ViewLikesComponent, canActivate:[AuthGuard]},
   {path:'collaborate/:username/:postId',component:CollabsComponent, canActivate:[AuthGuard]},
   {path:'participations',component:ParticipationsComponent, canActivate:[AuthGuard]},
   {path:'**',component:NotFoundComponent},
 ]
@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,
    RegisterComponent,
    PostComponent,
    ExploreComponent,
    EditComponent,
    LikeComponent,
    ViewLikesComponent,
    MyPipesPipe,
    CollborateComponent,
    CollabsComponent,
    ReversePipe,
    ParticipationsComponent, 
    IdfilterPipe, 
    AboutComponent, 
    NotFoundComponent,  
    AppErrorsComponent, 
    FooterComponent, 
    LoadingSpinnerComponent,  
    SquareSpinnerComponent,
    WindowSpinnerComponent
  
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    NgxPaginationModule,
   
    RouterModule.forRoot(appRoute)
   
  ],
  providers: [ValidateService,AuthService,PostsService,AuthGuard,CollaborateService,
    {provide: LocationStrategy, useClass: HashLocationStrategy},
    { provide: ErrorHandler, useClass: AppErrorHandler }],
  bootstrap: [AppComponent]
})
export class AppModule { }



