import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { AuthService } from './auth.service';

@Injectable()
export class CollaborateService {

  constructor(private http: Http, private auth: AuthService) { }
  getuserName() {
    return JSON.parse(localStorage.getItem('user')).username
  }

  requestToCollaborate(id, postWriter, status) {

    let username = this.getuserName()
    let post = { ready: status }
    return this.http

      .patch(`posts/requestCollaboration/${postWriter}/${username}/${id}`, post)
  }

  responseToCollab(id, person, post) {
    let username = this.getuserName()

    return this.http
      .patch(`posts/respondToCollaboration/${username}/${person}/${id}`, post)
  }

  addDiscussion(id, comment) {
    let post = {
      text: comment
    }
    let username = this.getuserName()
    return this.http
      .patch(`collab/discussions/${username}/${id}`, post)
  }
  participatios() {
    if (this.auth.isLoggedIn()) {
      let username = this.getuserName()
      return this.http.get(`collab/participations/${username}`)
    }
  }
  
  delNotifications(){
    let username = this.getuserName()
    return this.http.get(`collab/notifications/${username}`)
  }
}



