import { Component, OnInit } from '@angular/core';
import { AuthService } from '../service/auth.service';
import { PostsService } from '../service/posts.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  showSpinner = true
  user: any;
  name: String;
  posts: any;
  plen
  constructor(private auth: AuthService, private postsService: PostsService, private router: Router) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('user'))
    this.name = this.user.name
    this.postsService.userPosts(this.user.username).subscribe(res => {
      let p = res.json()
      this.posts = p.result;
      if (this.posts) {
        this.plen = this.posts.length
      }
      else {
        this.plen = 0
      }
      this.showSpinner = false;
    })


  }


  removePost(post) {
    if (confirm('Are you sure you wanted to delete This')) {
      this.postsService.deletePost(post._id).subscribe(res => {
        let status = res.json()
        if (status.success) {

          let index = this.posts.indexOf(post)
          this.posts.splice(index, 1)

          this.router.navigate(["/profile"])
        }
        else alert('Opps something went wrong try refreshing the page')
      })
    }
  }
}



