import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'myPipes'
})
export class MyPipesPipe implements PipeTransform {

  transform(value:String, args?: any): any {
    if(!value) return null;
    let date=value.substring(0,10)
    return date
  }

}



