import { Component, OnInit } from '@angular/core';
import { AuthService } from '../service/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent {
  username: String
  password: String
  email: String
  name: String
  show: boolean = false
  invalidDetails: boolean

  constructor(private auth: AuthService, private router: Router) { }
  status: any;
  submit(f: HTMLInputElement) {
    if (this.username && this.email && this.password && this.name) {
      this.show = true
      let user = { username: this.username, email: this.email, password: this.password, name: this.name }

      this.auth.registerUser(user).subscribe(res => {
        let status = res.json()
        console.log(status)
        if (status.success) {
          this.invalidDetails = false
          this.router.navigate(['/login'])
        }
        else if (status.success == false) {
          this.invalidDetails = true
          this.show = false
        }
      }, error => console.log(error))
    }
  }


}



