import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PostsService } from '../service/posts.service';
import { request } from 'https';
import { CollaborateService } from '../service/collaborate.service';


@Component({
  selector: 'app-view-likes',
  templateUrl: './view-likes.component.html',
  styleUrls: ['./view-likes.component.css']
})
export class ViewLikesComponent implements OnInit {
  showLoader:boolean=true
  likesLength
  title
  text
  username
  post: any;
  likedBy: any[]
  show: boolean
  id: String
  comments: any[]
  comment: any
  date: String
  commentsLen
  requestsForCollab: any[]
  isClicked: boolean
  display
  collaborations: any[]
  isCollabed: boolean
  isPostWriter:boolean
  len
  constructor(private route: ActivatedRoute,
    private postService: PostsService,
    private router: Router,
    private collaborateService: CollaborateService) { }

  ngOnInit() {
    let params = this.route.snapshot.params
    //console.log(params['id'])

    this.postService.getPost(params['id']).subscribe(res => {
      let post = res.json()
      this.post = post.result;
      this.title = this.post.title;
      this.text = this.post.text
      this.username = this.post.username
      this.likedBy = this.post.likedBy
      this.id = this.post._id
      this.date = this.post.createdAt
      this.comments = this.post.comments
      this.requestsForCollab = this.post.requestedForCollaboration
      this.collaborations = this.post.collaborations
      //console.log(this.requestsForCollab)
      this.len = this.collaborations.length
      this.likesLength=this.likedBy.length
      this.commentsLen=this.post.comments.length
      let username = JSON.parse(localStorage.getItem('user')).username;

     if(username===this.username){this.isPostWriter=true}
     else {this.isPostWriter=false}

      if (this.collaborations.indexOf(username) >= 0) { this.isCollabed = true }
      else { this.isCollabed = false }
      //console.log(this.isCollabed)
      //console.log(this.collaborations)
      if (this.requestsForCollab.indexOf(username) >= 0) {
        this.isClicked = true

      }
      else { this.isClicked = false };
      (this.isClicked) ? this.display = `Undo Collaboration request` : this.display = `Request to Join`
      this.showLoader=false
    })
    
  }

  toggle() {
    this.show = !this.show
  }
  submit() {
    if (this.comment) {
      let user = localStorage.getItem('user')
      
      let username = JSON.parse(user).name
      let userComment = `${username} : ${this.comment}`
      // console.log(this.comment)
      this.comments.splice(0, 0, userComment)
      this.postService.postComment(this.id, userComment).subscribe(res => {
        //  console.log(res)
        
        this.comment = null
        this.show = !this.show
      })
    }

  }
  request() {
    this.isClicked = !this.isClicked;
    //console.log(this.postId,this.postWriter,this.isClicked)
    this.collaborateService.requestToCollaborate(this.id, this.username, this.isClicked)
      .subscribe(res => {
        let response = res.json()

        if (response.success) {

          (this.isClicked) ? this.display = `Undo Collaboration request` : this.display = `Collaborate`
          alert(`${response.message}`)
        }

      })
  }

}


