import { Component, OnInit, DoCheck } from '@angular/core';
import { CollaborateService } from '../service/collaborate.service';
import { Router } from '@angular/router';
import { error } from 'selenium-webdriver';
import { AuthService } from '../service/auth.service';
import { PostsService } from '../service/posts.service';

@Component({
  selector: 'app-participations',
  templateUrl: './participations.component.html',
  styleUrls: ['./participations.component.css']
})
export class ParticipationsComponent implements OnInit {
  showSpinner=true
  inCollabortion: any[]
  notificatios: any[]
  info
  num
  user
  display
  posts
  lenofPosts
  constructor(private collabService: CollaborateService, private postService:PostsService,
    private router: Router) { }

  ngOnInit(): void {

    this.collabService.participatios().subscribe(res => {
      this.info = res.json()
      this.inCollabortion = this.info.result['inCollaboration']
      this.notificatios = this.info.result['notifications']
      this.num = this.notificatios.length
      //console.log(this.inCollabortion)
      //console.log(this.notificatios)
      this.showSpinner=false;
    })
    
    this.user=JSON.parse(localStorage.getItem('user'))
    this.postService.userPosts(this.user.username).subscribe(res=>{
      let p=res.json()
      this.posts=p.result;
      if(this.posts){
        this.lenofPosts=this.posts.length 
      }
      else{
        this.lenofPosts=0
      }
    })

  
    
  }
  goto(item: String) {
    let val = item.split('-')
    this.router.navigateByUrl(`/collaborate/${val[1]}/${val[0]}`)

  }
  toggle() {
    let val = this.notificatios.length
    this.notificatios.splice(0, val)
    this.num = this.notificatios.length;
   if(val>0){
    this.collabService.delNotifications().subscribe(res=>{
     let result=res.json()
     if(result.success){
      
       
     }
    },(error)=>alert('opps something went wrong'))
  }
}
  
}



