import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'idfilter'
})
export class IdfilterPipe implements PipeTransform {

  transform(value:String, args?: any): any {
if(value){
  let val=value.split('-')
  return val[2]
}

  else  return null;
  }

}



