import { Component, OnInit, DoCheck } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PostsService } from '../service/posts.service';


@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {
  showLoader:boolean=true
 _title:String;
 _text:String;
 postId;
  constructor(private route:ActivatedRoute,private postService:PostsService, private router:Router) { }

  ngOnInit() {
    let params = this.route.snapshot.params
   //console.log(params['id'])
   this.postId=params['id']
   let postWriter=params['postWriter']
   //console.log(postWriter)
   let user=JSON.parse( localStorage.getItem('user')).username
   if(user===postWriter)
  { 
   this.postService.getPost(params['id']).subscribe(res=>{
     let post=res.json()
     this._title = post.result.title;
     this._text=post.result.text;
    // console.log(post)
    this.showLoader=false;
   })}

   else{
     this.router.navigate(['/notfound'])
   }
      
  }
   submit(){
     let post={
       title:this._title,
       text:this._text
     }
     this.postService.updatePost(post,this.postId).subscribe(res=>{
       let status = res.json()
       if(status.success){
         this.router.navigate(['/profile'])
       }
       else this.router.navigate(['/dashboard'])
     })
   }
  }





